export default function CloudinaryQuery(imgPath) {
  return {
    data: {
      basePath: "https://images.aha.is/image/upload",
      imgPath: imgPath || "/b/o/bo_kunarkart.jpg",
      width: undefined,
      height: undefined,
      pad: false,
      background: "",
      dpa: "dpr_auto",
      trim: "e_trim:10",
      quality: "q_auto",
    },
    width(width) {
      this.data.width = width;
      return this;
    },
    height(height) {
      this.data.height = height;
      return this;
    },
    pad(usePad) {
      this.data.pad = usePad;
      return this;
    },
    dpa(dpaStr) {
      // https://cloudinary.com/documentation/image_transformations#set_device_pixel_ratio_dpr
      this.data.dpa = dpaStr;
      return this;
    },
    background(bgStr) {
      // https://cloudinary.com/documentation/image_transformations#setting_background_color
      // b_rgb:3e2222, b_green,  b_auto:predominant
      this.data.background = bgStr;
      return this;
    },
    trim(str) {
      this.data.trim = str;
      return this;
    },
    quality(str) {
      this.data.quality = str;
      return this;
    },
    toURL() {
      const { basePath, imgPath, width, height, trim, quality } = this.data;
      const paramList = [
        width && `w_${width}`,
        height && `h_${height}`,
        quality,
      ].filter(Boolean);
      return `${basePath}/${trim}/${paramList.join(",")}/products/${imgPath}`;
    },
  };
}
