<template>
  <div
    :class="[
      'grid-grocery-item',
      showQuantityControlInMobile &&
        'grid-grocery-item--show-qty-control-mobile',
    ]"
  >
    <div v-if="!entity_id" class="items-grid__item">
      <div class="items-grid__image app-ph" />
      <div class="items-grid__placeholder-text app-ph" />
    </div>
    <div
      v-else
      :class="[
        'items-grid__item',
        isOverlayShowing && 'items-grid__item--show-overlay',
        isInGroceriesScreen && 'cursor-pointer',
      ]"
      @click="showGroceryProductModal"
    >
      <div class="items-grid__image" @click="toggleOverlay">
        <span
          class="items-grid__item-discount-percent"
          v-if="percentDiscount"
          >{{ percentDiscount }}</span
        >
        <div>
          <ImageViewer
            :src="cloudinaryImage"
            class="img img-responsive"
            :alt="name"
            loading="lazy"
          />
        </div>
        <div class="items-grid__item-in-cart" v-if="this.quantity">
          {{ this.quantity }}
        </div>
        <div class="item-grid__image-overlay">
          <base-button
            v-if="!this.quantity"
            class="center-center width primary"
            @click.native="handleIncreaseCartQty"
            >{{ $t("addToCart") }}</base-button
          >
          <div v-else class="center-center width">
            <div class="quantity">{{ this.quantity }} {{ $t("inCart") }}</div>

            <div class="item-grid__overlay-btns-container">
              <base-button
                class="primary"
                size="small"
                @click.native="handleDecreaseCartQty"
              >
                <i class="fa fa-minus" />
              </base-button>

              <base-button
                class="primary btn--small btn--fit-width"
                @click.native="handleIncreaseCartQty"
              >
                <i class="fa fa-plus" />
                {{ $t("add") }}
              </base-button>
            </div>
          </div>
        </div>
      </div>
      <div class="items-grid__content">
        <div class="items-grid__name">
          <router-link :to="urlStripped">{{ translatedName }}</router-link>
        </div>
        <div class="details">
          <span v-show="calc_special_price" class="special_price">{{
            calc_special_price | formatISK
          }}</span>
          <span class="price">{{ calc_price | formatISK }}</span>
          <span class="unit_text">{{ unit_text }}</span>
        </div>
        <QuantityControl
          class="quantity-control--specific-store"
          :updateQuantity="updateCartQuantity"
          :quantity="quantity"
        />
      </div>
    </div>
  </div>
</template>

<i18n>
{
  "en": {
    "inCart": "in cart",
    "add": "Add"
  },
  "is": {
    "inCart": "í körfu",
    "add": "Bæta við"
  }
}
</i18n>

<script type="text/javascript">
import BaseButton from "@/components/BaseButton";
import ImageViewer from "@/components/ImageViewer";
import CartItemMixin from "../mixins/CartItemMixin";
import get from "@/util/get";
import { MODAL_KEY_PRODUCT } from "@/store/modalModule";
import { mapState } from "vuex";
import QuantityControl from "@/components/QuantityControl";
import getStringByLocale from "@/util/getStringByLocale";
import CloudinaryQuery from "@/util/CloudinaryQuery";

export default {
  name: "GridGroceryItem",
  mixins: [CartItemMixin],
  components: {
    BaseButton,
    ImageViewer,
    QuantityControl,
  },
  props: {
    name: String,
    en_name: String,
    image: { type: String, default: "" },
    entity_id: Number,
    objectID: String,
    stores: Array,
    catname: String,
    unit_text: String,
    price: Number,
    url: String,
    special_price: [String, Number],
    ingredients: String,
    showQuantityControlInMobile: {
      type: Boolean,
      default: false,
    },
    imgHeight: {
      type: Number,
      default: 400,
    },
  },
  data: function() {
    return {
      isOverlayShowing: false,
    };
  },
  computed: {
    ...mapState(["isInGroceriesScreen"]),
    imgRelativeURL() {
      return this.image && this.image.includes("http")
        ? this.image.replace("https://cdn.aha.is/media/catalog/product/", "")
        : this.image;
    },
    cloudinaryImage() {
      if (this.image === "no_selection" || !this.image) return "";

      return new CloudinaryQuery(this.imgRelativeURL)
        .height(this.imgHeight)
        .toURL();
    },
    calc_special_price() {
      if (this.special_price && this.special_price !== this.price)
        return this.price;
      return "";
    },
    calc_price() {
      if (this.special_price) {
        return parseInt(this.special_price);
      } else {
        return this.price;
      }
    },
    percentDiscount: function() {
      const { calc_special_price, calc_price } = this;
      if (calc_special_price) {
        return (
          "- " +
          (100 - Math.ceil((calc_price * 100) / calc_special_price)) +
          "%"
        );
      }
      return "";
    },
    urlStripped() {
      return this.url ? this.url.replace("https://www.aha.is", "") : "";
    },
    translatedName() {
      return getStringByLocale({ isStr: this.name, enStr: this.en_name });
    },
  },
  methods: {
    showGroceryProductModal() {
      const {
        name,
        calc_special_price,
        calc_price,
        unit_text,
        imgRelativeURL,
        cartItem,
        ingredients,
      } = this;
      // this function only be called in Groceries screen
      if (!this.isInGroceriesScreen) return;

      this.$store.dispatch("toggleModal", {
        modalKey: MODAL_KEY_PRODUCT,
        isVisible: true,
        name,
        unit_text,
        calc_special_price,
        calc_price,
        productImg: imgRelativeURL,
        inCartQty: get(cartItem, "cart_qty", 0),
        updateCartQuantity: this.updateCartQuantity,
        ingredients,
      });
    },
    handleIncreaseCartQty: function(e) {
      e.stopPropagation();
      this.increaseCartQty();
    },
    handleDecreaseCartQty: function(e) {
      e.stopPropagation();
      this.decreaseCartQty();
    },
    toggleOverlay: function(e) {
      e.stopPropagation();

      this.isOverlayShowing = true;
      clearTimeout(this.overlayActiveTimer);
      this.overlayActiveTimer = setTimeout(
        () => (this.isOverlayShowing = false),
        3000
      );
    },
  },
};
</script>

